import React, { Component, Fragment } from "react";
import Page from "../layouts/main";
import Contact from "../sections/Contact/Contact";
import settings from "../util/settings";
import Feature from '../components/Feature/Feature';
import Testimonials from '../sections/Testimonials/Testimonials';
import MedicalSVG from '../static/svg/medical.svg?include';
import CareSVG from '../static/svg/care.svg?include';
import HomeSVG from '../static/svg/home.svg?include';
import LogoSVG from '../static/svg/logo.svg?include';
import Head from "next/head";
import ReactGA from 'react-ga';

class Index extends Component {

  constructor(props) {
    super(props);
    this.state = {
      animDone: true
    }
    this.AnimationElements = []
    this.ASSET_PATH = settings.ASSET_PATH + "images/landing/";
  }

  componentDidMount() {
    TweenMax.set(this.container, {autoAlpha: 0});
    TweenMax.set(this.AnimationElements, {autoAlpha: 0});

    this.animateIn();
  }

  animateIn = () => {
    var tl = new TimelineMax({paused: true, delay: 0.5});
    tl.to(this.container, 1, {autoAlpha: 1});
    tl.staggerFromTo(this.AnimationElements, 2, {autoAlpha: 0, y: 50}, {autoAlpha: 1, y: 0, ease: Expo.easeOut}, 0.25);
    tl.play();
  }

  render() {
    this.AnimationElements = []
    return (
      <Page id="Home" menu={{ show: this.state.animDone }}>

        <div
          id="section-home"
          ref={el => this.container = el}
          style={{ opacity: 0 }}
        >
          <div style={{ backgroundColor: settings.colors.primaryDark }}>
            <img id="landing-image" src={this.ASSET_PATH + "intro.png"} />
            <div dangerouslySetInnerHTML={{ __html: LogoSVG }} />
            {/* <a id="free-assessment" href="tel:18008502052">Call <strong>1–800-850-2052</strong><br/>for a FREE assessment</a> */}

            <div className="row intro-content">
              <div className="offset-1 col-10 col-md-8 col-lg-6 col-xl-4">
                <h1>Providing Families<br/>with Peace of Mind</h1>
                <p>We believe life is a gift. Hospall Private Homecare provides peace of mind to families with the health and care needs of their loved ones in King Township, City of Vaughan, and the surrounding GTA. Our commitment is to maintain open lines of communication within our multi disciplinary team to ensure the care is being provided consistent to the client. Our word is our promise.</p>
                <a className="cta" href="/services">Learn More</a>
              </div>
            </div>
          </div>

          <div className="services-briefly">
            <div className="row services-briefly-intro">
              <div className="col-12 col-sm-6 col-lg-6 flex-center services-briefly-intro-img">
                <img style={{maxWidth: "100%"}} src={this.ASSET_PATH + "playing-chess.jpg"} />
              </div>
              <div className="col-12 col-sm-6 offset-lg-1 col-lg-5 flex-center services-briefly-intro-info" style={{ justifyContent: 'flex-start' }}>
                <h2 className="dark" style={{ marginBottom: 30 }}>How We Can Help</h2>
                <p className="dark">By providing quality, personalized private our trained staff will provide a level of professionalism that allows for healthy, safe and positive interactions. We provide a variety of services to suit the needs of you and your loved ones.</p>
                <a className="cta dark" href="/services">View Services</a>
              </div>
            </div>

            <div className="row d-none d-sm-flex">
              <div className="col-sm-6 col-md-4">
                <Feature title="Private Home Care" icon={HomeSVG}
                        text="We specialize but are not limited to provide live-in support including short-term postoperative care. We also organize and help clients with daily activities to help to stimulate their mind and enrich their interests. We are flexible to build a care plan that is best for our clients." />
              </div>
              <div className="col-sm-6 col-md-4">
                <Feature title="Home Medical Services" icon={MedicalSVG}
                        text="We provide medical and personal home care services by skilled professionals, including help with personal hygiene. We also provide medical services such as bloodwork and medication administration, and accompanying clients to appointments." />
              </div>
              <div className="col-sm-6 col-md-4">
                <Feature title="Respite Care" icon={CareSVG}
                        text="Caring for loved ones can be a full-time job and we recognize it can be exhausting. When you need a rest, we are here to relieve you. We can provide 24-hour care in the comfort of our client’s own home." />
              </div>
            </div>
          </div>

          <Testimonials />

          <Contact description='In order to maintain the highest quality of care for our clients and their families, we ensure each case is evaluated by an assessment expert. Let’s work together to find the best type of care for you and your loved one.' />

        </div>

      </Page>
    )
  }
}

export default Index
